.note-editor {
    position: relative;
    border: 1px solid #e5e5e5
}

.note-editor .note-dropzone {
    position: absolute;
    z-index: 100;
    display: none;
    color: #87cefa;
    background-color: white;
    border: 2px dashed #e5e5e5;
    opacity: .95;
    pointer-event: none
}

