    display: table-cell;
    font-size: 28px;
    font-weight: bold;
    text-align: center;
    vertical-align: middle
}

.note-editor .note-dropzone.hover {
    color: #098ddf;
    border: 2px dashed #e5e5e5
}

.note-editor.dragover .note-dropzone {
