}

.note-editor .note-toolbar {
    background-color: #f5f5f5;
    border-bottom: 1px solid #e5e5e5
}

.note-editor.fullscreen {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1050;
    width: 100%
}

.note-editor.fullscreen .note-editable {
