@import "variables.less";
@import "mixins.less";
/*-- 2.12 Timeline --*/
//Background Colors
.t-bg-green {
    background-color: @green_block;
}

.t-bg-blue {
    background-color: @blue_block;
}

.t-bg-orange {
    background-color: @orange_block;
}

.t-bg-gray {
    background-color: @gray_block;
}

.t-bg-violet {
    background-color: @violet_block;
}

.t-bg-tur {
    background-color: @tur_block;
}

.t-bg-brown {
    background-color: @brown_block;
}

.timeline-large {
    list-style: none;
    padding: 20px 0 20px;
    position: relative;
    &:before {
        top: 0;
        bottom: 0;
        position: absolute;
        content: " ";
        width: 2px;
        background-color: #eeeeee;
        left: 50%;
        margin-left: -1.5px;
    }
    & > li {
        margin-bottom: 20px;
        position: relative;
        &:before, &:after {
            content: " ";
            display: table;
        }
        &:after {
            clear: both;
        }
        &> .timeline-panel {
            width: 44%;
            float: left;
            border: 1px solid #eee;
            position: relative;
            .timeline-heading {
                display: table;
                border-bottom: #eee 1px solid;
                width: 100%;
                & > div {
                    display: table-cell;
                    vertical-align: middle;
                }
                .timeline-h-thumb {
                    width: 60px;
                    height: 60px;
                    padding: 15px 0px 15px 15px;
                    img {
                        width: 60px;
                        height: 60px;
                        .rounded(100%);
                    }
                }
                .timeline-h-info {
                    padding: 15px;
                }
            }
            .timeline-title {
                font-size: 18px;
                font-weight: 400;
                margin-top: 0px;
                margin-bottom: 6px;
            }
            .timeline-content {
                padding: 15px;
            }
            &:before {
                position: absolute;
                top: 26px;
                right: -15px;
                display: inline-block;
                border-top: 15px solid transparent;
                border-left: 15px solid #ddd;
                border-right: 0 solid #ddd;
                border-bottom: 15px solid transparent;
                content: " ";
            }
            &:after {
                position: absolute;
                top: 27px;
                right: -14px;
                display: inline-block;
                border-top: 14px solid transparent;
                border-left: 14px solid #fff;
                border-right: 0 solid #fff;
                border-bottom: 14px solid transparent;
                content: " ";
            }
        }
        .timeline-badge {
            color: #fff;
            width: 50px;
            height: 50px;
            padding: 3px;
            font-size: 24px;
            position: absolute;
            display: inline-block;
            .rounded(100%);
            top: 16px;
            left: 50%;
            margin-left: -25px;
            background-color: #ddd;
            z-index: 100;
            & > span {
                width: 44px;
                height: 44px;
                background-color: #fff;
                display: inline-block;
                .rounded(100%);
                i {
                    text-align: center;
                    width: 44px;
                    height: 44px;
                    line-height: 44px;
                    display: inline-block;
                    color: #555;
                }
            }
        }
        &.timeline-invert {
            & > .timeline-panel {
                float: right;
                &:before {
                    border-left-width: 0;
                    border-right-width: 15px;
                    left: -15px;
                    right: auto;
                }
                &:after {
                    border-left-width: 0;
                    border-right-width: 14px;
                    left: -14px;
                    right: auto;
                }
            }
        }
    }
}

