@media (max-width: 767px) {
    .timeline-large {
        & > li {
            & > .timeline-badge {
                left: 15px;
                margin-left: 0;
                top: 16px;
            }
            &> .timeline-panel {
                float: right;
                width: 70%;
                &:before {
                    border-left-width: 0;
                    border-right-width: 15px;
                    left: -15px;
                    right: auto;
                }
                &:after {
                    border-left-width: 0;
                    border-right-width: 14px;
                    left: -14px;
                    right: auto;
                }
            }
        }
        &:before {
            left: 40px;
        }
    }
}