@import "variables.less";
@import "mixins.less";
.iconic-view {
    .large-logo {
        display: none;
    }
}

.pin-it {
    .iconic-logo {
        display: none !important;
    }
}

.iconic-view {
    .iconic-logo {
        display: block !important;
    }
}

@media screen and (max-width: 1000px) {
    .top-mid,
    .desktop-search {
        display: none;
    }
    .top-aside-right {
        position: absolute;
        top: -@IconicViewWidth;
        right: @IconicViewWidth;
    }
    .mobile-search-btn {
        display: block;
        width: @IconicViewWidth;
        height: @IconicViewWidth;
        line-height: @IconicViewWidth;
        text-align: center;
        position: absolute;
        top: 0px;
        left: @IconicViewWidth;
        font-size: 18px;
    }
    .iconic-logo {
        display: none;
    }
    .page-content {
        margin-left: 0px;
    }
    .left-aside {
        z-index: 8000;
    }
    .iconic-view {
        .page-content {
            margin-left: 0px;
        }
        .left-aside {
            left: -@IconicViewWidth;
        }
    }
    .hide-list-menu {
        .page-content {
            margin-left: 0px;
        }
        .left-aside {
            left: 0px;
        }
    }
    .list-menu-view {
        .page-content {
            margin-left: 0px;
        }
        .left-aside {
            left: -@LeftAsideWidth;
        }
    }
    .pin-it {
        .page-content {
            margin-left: 0px;
        }
    }
    .hide-aside-left {
        left: -@LeftAsideWidth;
    }
    .top-bar-action {
        min-height: @IconicViewWidth;
        .rightbar-action {
            display: block;
        }
    }
    .top-aside-right {
        .rightbar-action {
            display: none;
        }
    }
    .aside-close {
        display: block;
    }
    .leftbar-action{
        display: none;
    }
    .leftbar-action-mobile {
        display: block;
    }
    .m-aside-action {
        display: block;
    }
    .h-iconic-view {
        .left-aside {
            left: -@LeftAsideWidth;
            width: @LeftAsideWidth;
        }
    }
    .user-nav {
        margin-right: 0px;
    }
    .notification-nav {
        margin-left: 0px;
    }
    .top-bar {
        .container-fluid {
            margin-left: 0px;
            margin-right: 0px;
        }
    }
    // Inbox
    .inbox-container {
        .inbox-sidebar {
            z-index: 2000;
            left: -@InboxSidebarWidth;
        }
        .inbox-content {
            margin-left: 0px;
            .email-list {
                width: 100%;
                z-index: 1600;
            }
            .mail-body {
                z-index: 1800;
                position: fixed;
                width: 100%;
                height: 100%;
                margin-left: 0px;
                right: -100%;
                background-color: #fff;
                .transition(.2s, ease-out);
            }
        }
        &.hide-inbox-sidebar {
            .inbox-sidebar {
                left: 0px;
                .transition(.2s, ease-out);
            }
            .inbox-content {
                margin-left: 0px;
                .transition(.2s, ease-out);
            }
        }
        &.mobile-mail-show {
            .inbox-content {
                .mail-body {
                    right: 0px;
                    .transition(.2s, ease-out);
                }
            }
        }
        .mobile-mail-action {
            display: block;
        }
    }
    .mail-body {
        &.multi-page {
            .email-content {
                margin: 0px;
            }
        }
    }
    .compose-form,
    .email-list-toolbar {
        width: 100%;
    }
    // Note
    .note-container {
        .note-sidebar {
            z-index: 2000;
            left: -160px;
        }
        .note-content {
            margin-left: 0px;
            .email-list {
                width: 100%;
                z-index: 1600;
            }
            .mail-body {
                z-index: 1800;
                position: fixed;
                width: 100%;
                height: 100%;
                margin-left: 0px;
                right: -100%;
                background-color: #fff;
                .transition(.2s, ease-out);
            }
        }
        &.hide-note-sidebar {
            .note-sidebar {
                left: 0px;
                .transition(.2s, ease-out);
            }
            .inbox-content {
                margin-left: 0px;
                .transition(.2s, ease-out);
            }
        }
        &.mobile-mail-show {
            .inbox-content {
                .mail-body {
                    right: 0px;
                    .transition(.2s, ease-out);
                }
            }
        }
        .mobile-mail-action {
            display: block;
        }
    }
    .note-list-wrap {
        width: 100% !important;
        z-index: 1600;
    }
    .note-list-toolbar {
        width: 100%;
        .note-search-form {
            width: 100%;
        }
    }
    .note-body {
        z-index: 1800;
        position: fixed;
        width: 100% !important;
        height: 100%;
        margin-left: 0px;
        right: -100%;
        z-index: 6800 !important;
        background-color: #fff;
        .transition(.2s, ease-out);
    }
    .mobile-note-show {
        .note-body {
            z-index: 1800;
            position: fixed;
            width: 100% !important;
            height: 100%;
            margin-left: 0px;
            right: 0px;
            z-index: 8800 !important;
            background-color: #fff;
            .transition(.2s, ease-out);
        }
    }
    // Task
    .task-container {
        .task-sidebar {
            z-index: 2000;
            left: -200px;
        }
        .task-content {
            margin-left: 0px;
            .task-list {
                width: 100%;
                z-index: 1600;
            }
            .task-body {
                z-index: 1800;
                position: fixed;
                width: 100%;
                height: 100%;
                margin-left: 0px;
                right: -100%;
                background-color: #fff;
                .transition(.2s, ease-out);
            }
        }
        &.hide-task-sidebar {
            .task-sidebar {
                left: 0px;
                .transition(.2s, ease-out);
            }
            .task-content {
                margin-left: 0px;
                .transition(.2s, ease-out);
            }
        }
        &.mobile-task-show {
            .task-content {
                .task-body {
                    right: 0px;
                    .transition(.2s, ease-out);
                }
            }
        }
        .mobile-task-action {
            display: block;
        }
    }
    .task-body {
        z-index: 1800;
        position: fixed;
        width: 100% !important;
        height: 100%;
        margin-left: 0px;
        right: -100%;
        z-index: 6800 !important;
        background-color: #fff;
        .transition(.2s, ease-out);
    }
    .mobile-task-show {
        .task-body {
            z-index: 1800;
            position: fixed;
            width: 100% !important;
            height: 100%;
            margin-left: 0px;
            right: 0px;
            z-index: 8800 !important;
            background-color: #fff;
            .transition(.2s, ease-out);
        }
    }
    .task-list-toolbar {
        width: 100%;
        .task-search-form {
            width: 100%;
        }
    }
    // Forum
    .forum-list-container {
        margin-right: 0px;
    }
    .forum-categories-show {
        -ms-transform: translate(100%, 0px) !important;
        -webkit-transform: translate(100%, 0px) !important;
        transform: translate(100%, 0px)!important;
        -webkit-transition: transform .1s linear;
        transition: transform .1s linear;
    }
    .forum-categories-hide {
        -ms-transform: translate(0px, 0px) !important;
        -webkit-transform: translate(0px, 0px) !important;
        transform: translate(0px, 0px) !important;
        -webkit-transition: transform .1s linear;
        transition: transform .1s linear;
    }
    .forum-aside-toggle {
        display: block;
    }
    .forum-categories {
        width: 287px;
        z-index: 6000;
    }
}

@media screen and (max-width: 790px) {
    .desktop-search {
        display: none;
    }
    .mobile-search {
        display: block;
    }
    .top-bar {
        .notification-nav {
            float: left;
            width: auto;
        }
    }
}

@media screen and (max-width: 460px) {
    .top-bar {
        .notification-nav {
            float: left;
            width: auto;
        }
    }
    .user-nav {
        float: none;
        .user-info {
            display: none !important;
        }
        .dropdown-menu {
            left: -88px;
        }
        & > ul {
            & >li {
                & >a {
                    color: @TopBarLinkColor;
                    display: block;
                    min-height: 61px;
                    padding: 5px 10px;
                }
            }
            & >li.open {
                & >a {
                    color: @PrimaryTextColor;
                    display: block;
                    min-height: 61px;
                    padding: 5px 10px;
                }
            }
        }
        .user-thumb {
            margin: 0px;
            text-align: center;
        }
    }
    .mobile-search {
        float: right !important;
    }
    .responsive-fix {
        clear: both;
    }
}